
@media only screen and (min-width: 320px) and (max-width: 600px) {
    .Box  {
            background: linear-gradient(-16deg, rgb(66, 23, 130) 15%, transparent 13%, transparent 90%, rgb(66, 23, 130) 18%)!important;
        }
    
    .main-cont {
        width:100%;
        margin-top: 150px;
    }
    .filters {
        width: 95%;
        border-radius: 4px;
        box-shadow: 0 0 10px -2px grey;
        border: 2px solid #421782;
        position: fixed;
        margin: 0 10px;
        bottom: 0;
        display: none;
    }
    .inner-filters {
        width: 100%;
        padding: 0;
        flex-direction: column;
        margin: 0 auto;
    }
    
    .filters p {
        font-size: 11px;
        margin-left: 15px;
        margin-bottom: 0;
    }   
    .sidebar{
        width: 80%;
    } 
    .category {
        margin-right: 0;
    }
    .category button, .filter-btn {
        padding: 0;
        font-size: 9px;
        flex-basis: 30%;
        height: 32px;
    }
    .category div {
        justify-content: center;
    }
    .time-frame {
        flex-basis: 63%;
    }
    .time-frame, .group-size {
        margin: 0;
    }
    .time-frame button, .group-size button {
        flex-basis: 33%;
    }
    .time-frame div, .group-size div {
        margin-left: 15px;
    }
    .group-size div {
        justify-self: start;
    }
    .Menu {
        flex-wrap:wrap;
        border-bottom: none;
    }
    .menu, .logo{
        margin-bottom:14px;
    }
    .menu{
        flex-basis: 27%;
    }
    .menu-btn{
        flex-basis: 100%;
        justify-content: center;
        margin-top: 25px;
    }
        .assessment-btn, .menu-btn1, .menu-btn2{
        padding: 7px 16px;
        font-size: 10px;
    }
    .Menu .header-firstP {
        display: block;
        margin: 0 auto;
        padding: 0;
        font-size: 9px;
    }
    .main-cont .header-firstP {
        display: none;
    }
    .header {
        width: 80%;
    margin: 0 auto;

    }
    .header h1 {
        font-size: 22px;
        }
     
        header p {
            margin-bottom: 0;
        }
    .games-list a {
        width: 100%;
    }
    .games-list a .game {
        flex-basis: 100%;
    }
    .game .img-game {
        display: none;
    }
    .game {
        height: fit-content;
    }
    .game .game-desc {
        border-radius: 5px;
    }
    .footer {
        flex-direction: column-reverse;
    }
    .footer-menu, .footer-logo, .footer-links{
        width: 100%;
    }
    .footer-links{ order: 3;}
    .footer-menu{order: 2; margin: 18px 0;}
    .footer-logo{order: 1;}

    .hideFilter{
        display: none;
    }
    .showFilter {
        display: block;
    }
    .render-game .title {
        font-size: 25px;
        margin-left: 15px;
    }
    .game-spec {
        flex-direction: column;
        width: 90%;
    }
    .game-spec-categories {
        width: 100%;
    }
    .filter-btn {
        padding: 10px 0;
        font-size: 13px;
    }
    .filter-games-button, .filter-close-btn {
        width: 94%;
        display: flex;
        justify-content: space-evenly;
        box-shadow: 0 0 10px -2px gray;
        cursor: pointer;
        background-color: #fcd232;
        border-radius: 4px;
    }
    .filter-games-button{
        position: fixed;
        bottom: 10px;
        left: 10px;
    }
    .filter-close-btn {
        margin: 5px auto;
    }

    .filter-games-button button, .filter-close-btn button  {
        background: transparent;
        border: none;
        font-weight: bold;
    }
    .filter-games-button span, .filter-close-btn span {
        margin: 7px;
    }
}
@media only screen and (min-width: 425px) and (max-width: 600px) {
    .filter-games-button {
        width: 96%;
    }
}

@media only screen and (min-width: 600px) and (max-width: 768px){
    .main-cont{
        width: 95%;
    }
   
    .games-list {
        width: 100%;
        justify-content: space-evenly;
        margin-top: 20px;
    }
    .games-list a {
        width: 33%;
    }
    .game {
        flex-basis: 33%;
        margin: 5px;
    }
    
    .game-desc {
        padding-top: 10px;
    }
    .game-spec {
        flex-direction: row;
        width: 95%;
        padding: 25px 5px 10px 5px;
    }
    .game-time-frame {
        font-size: 11px;

    }
    .game-minutes {
        font-size: 9px;
    }
    .game-txt {
        padding-left: 5px;
    }
    .img-icon {
        width: 30px;
        height: 30px;
    }
    .menu-btn1, .menu-btn2 {
        font-size: 9px;
    }
    .filters {
        width: 100%;
        position: fixed;
        bottom: 1px;
        border: 2px solid #421782;
        margin: 0;
        left: 0;
        display: none;
    }

    .header-firstP {
        padding: 10px;
        margin-top: 87px;
    }
    .header h1 {
        font-size:  22px;
        margin-bottom: 20px;
    }
    .assessment-btn {
        font-size: 11px;
    }
    .filters p {
        font-size: 13px;
    }
    .inner-filters{
        width: 100%;
        padding-left: 5px;
        margin: 0 5px;
        flex-direction: column;
    }
    .category, .time-group {
        flex-basis: 49%;
    }
    .time-group {
        margin-right: 10px;
    }
    .category button, .filter-btn {
        padding: 0;
        font-size: 9px;
        flex-basis: 30%;
        height: 30px;
    }
    .time-frame, .group-size {
        margin-left: 0;
    }
    .hideFilter{
        display: none;
    }
    .showFilter {
        display: block;
    }
    .footer-menu a {
        font-size: 13px;
    }
    .sidebar {
        width: 50%;
    }
    .filter-btn {
        padding: 10px 0;
        font-size: 13px;
    }
    .filter-games-button, .filter-close-btn {
        width: 97%;
        display: flex;
        justify-content: space-evenly;
        box-shadow: 0 0 10px -2px gray;
        cursor: pointer;
        background-color: #fcd232;
        border-radius: 4px;
    }
    .filter-games-button{
        position: fixed;
        bottom: 10px;
        left: 14px;
    }
    .filter-close-btn {
        margin-bottom: 5px;
    }

    .filter-games-button button, .filter-close-btn button  {
        background: transparent;
        border: none;
        font-weight: bold;
    }
    .filter-games-button span, .filter-close-btn span {
        margin: 7px;
    }
}
@media only screen and (min-width: 768px) and (max-width: 850px){
    .inner-filters {
        width: 95%;
    }
    .filters p {
        font-size: 11px;
        }
    .filter-btn {
        padding: 10px 0;
        font-size: 11px;
        flex-basis: 30%;
    }
    .time-frame button {
        flex-basis: 47%;
    }
}
@media only screen and (min-width: 850px) and (max-width: 1024px){
.sidebar{
    width: 30%;
}
.filter-btn {
    padding: 10px 0;
    font-size: 12px;
}
.filters p {
    font-size: 12px;
    }
    .category{
    flex-basis:58%;
    }
    .group-size {
    flex-basis: 54%;
    }
    .group-size button {
        flex-basis: 30%;
    }
}